<template>
    <div class="customer-page" 
        v-loading.fullscreen.lock="loading" element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.7)" style="width: 100%">
        <div class="header">
            <div class="header-item padding-right" :class="{headerItemActive:customerHeadTab == 0}" @click="postHeadTapBtn(0)">
                <span>已审核</span>
                <span class="right-red-slot" v-if="recruitingNum > 0">{{recruitingNum}}</span>
                <i :class="{isFirstActive:customerHeadTab == 0}"></i>
            </div>
            <div class="header-item padding-right" :class="{headerItemActive:customerHeadTab == 1}" @click="postHeadTapBtn(1)">
                <span>审核中</span>
                <span class="right-red-slot" v-if="waitingNum > 0">{{waitingNum}}</span>
                <i :class="{isFirstActive:customerHeadTab == 1}"></i>
            </div>
            <div class="header-item" :class="{headerItemActive:customerHeadTab == 2}" @click="postHeadTapBtn(2)">
                <span>审核失败</span>
                <span class="right-red-slot" v-if="underReviewNum > 0">{{underReviewNum}}</span>
                <i :class="{isActive:customerHeadTab == 2}"></i>
            </div>
            <div class="header-item" :class="{headerItemActive:customerHeadTab == 3}" @click="postHeadTapBtn(3)">
                <span>解除合作</span>
                <span class="right-red-slot" v-if="hasRemovedNum > 0">{{hasRemovedNum}}</span>
                <i :class="{isActive:customerHeadTab == 3}"></i>
            </div>
        </div>
		<div class="zhanwei"></div>
		<div class="list-customer" v-if="listShow && qdsMCertificationStatus == 3">
			<div class="list-item" v-for="(item, index) in listData" :key="index">
				<div class="item-top">
					<img :src="item.logoUrl" alt="" @click="goDetail(item.customerId)">
					<div :class="customerHeadTab == 0 ? 'item-top-middle' : 'item-top-middle item-top-middle-other'" @click="goDetail(item.customerId)">
						<p>{{item.customerCorpName}}</p>
						<p v-if="customerHeadTab == 0">{{stateTime}}: {{item.checkSuccessTime}}</p>
                        <p v-if="customerHeadTab == 1">{{stateTime}}: {{item.customerCreateTime}}</p>
                        <p v-if="customerHeadTab == 2">{{stateTime}}: {{item.checkFailedTime}}</p>
                        <p v-if="customerHeadTab == 3">{{stateTime}}: {{item.removeTime}}</p>
					</div>
					<p class="item-top-right"  v-show="customerHeadTab == 0" @click="disengagement(item.customerId)">解除合作</p>
				</div>
				<div class="item-body">
					<div class="item-body-left">
						<p @click="goPosition(item.customerCorpName)">{{item.demandCount}}</p>
						<p v-if="customerHeadTab == 0">正在招聘职位</p>
						<p v-else>关联职位</p>
					</div>
					<div class="line"></div>
					<div class="item-body-left" @click="goJobSeek(item.ehrId)">
						<p>{{item.resumeCount}}</p>
						<p>已投递简历</p>
					</div>
				</div>
				<div class="item-foot" v-show="customerHeadTab == 2">
					<p @click="failReason(item.certificationRemark)" class="bg-fail">失败原因</p>
					<p @click="edit(item.id,item.certificationRemark)">重新审核</p>
				</div>
				<div class="item-foot" v-show="customerHeadTab == 3">
					<p v-if='item.removeReason' @click="cancelReason(item.removeReason)" :class="item.removeReason ? 'bg-success': 'bg-fail'">解除原因</p>
					<p @click="recooperate(item.customerId)">重新合作</p>
				</div>
			</div>
			<infinite-loading @infinite="infiniteRecruiting" v-if="listData.length >= 10">
                    <span slot="spinner" class="infinite-load">
                        <i class="el-icon-loading"></i>
                        <span>亲爱的，您慢点，我加载呢~</span>
                    </span>
                    <span slot="no-results" class="infinite-load">
                        数据已全部加载
                    </span>
                    <span slot="no-more" class="infinite-load">
                        数据已全部加载
                    </span>
                </infinite-loading>
		</div>
		<div v-if='!listShow && qdsMCertificationStatus == 3'>
			<div class="list-no-data">
                <img :src="recruitingListBack" class="bo-data-icon">
                <p class="recruiting-tips">该状态下还没有客户</p>
            </div>
		</div>
        <div v-show="qdsMCertificationStatus != 3">
            <div class="list-no-data">
                <img :src="recruitingListBack" class="bo-data-icon">
                <p class="recruiting-tips">企业认证后即可解锁该模块哦~</p>
            </div>
        </div>
        <nav-tab :fatherActive='active'></nav-tab>
    </div>
</template>

<script>
import Util from '@/js/mUtils';
import navTab from "@/components/layout/tab.vue";
import {DLDict,DLArea,DLPType,DLIndustry} from '@/js/dl-code';
import recruitingListBack from "@/assets/post/recruiting_list_back.png";
import resumePicOne from "@/assets/qds-img/boy_1.png";
import { MessageBox } from 'mint-ui';
import InfiniteLoading from 'vue-infinite-loading';                     //分页加载组件
import { ifError } from 'assert';
export default {
    data() {
        return {
            globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            customerHeadTab: 0,
            clientHeight: {
                height: ""
            },
            recruitingListBack: recruitingListBack, //没有数据
            resumePicOne: resumePicOne,
            recruitingNum: '',                  // 已审核数量
            waitingNum: '',                     // 审核中数量
            underReviewNum: '',                 // 审核失败数量
            hasRemovedNum: '',                  // 解除合作
            loading: false,
			active: '1',
			listData: [],  // 列表数据
			currentPage: 1, // 当前页
			limitNum: 10, // 每页做多多少条数据
			listShow: true,
			stateTime: '合作时间' ,    // 四种状态下的对应时间
			state: {state:2},      // 认证状态
            customerId: '' ,   // 客户id
            qdsMCertificationStatus: '', // 认证状态
        }
    },
    components: {
        // Toast,
        navTab,
        InfiniteLoading,        //分页
    },
    created() {
        window.addEventListener('resize', this.getHeight);
        this.getHeight()
    },
    mounted() {
        this.qdsMCertificationStatus = sessionStorage.getItem('qdsMCertificationStatus',)             //认证状态数字 
        if(this.qdsMCertificationStatus == 3){
            this.getListData()
            this.statistics()
        }
        window.scrollTo(0, 0);
        
    },
    destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
    methods: {
        getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
        goPosition(customerCorpName) {
            this.$router.push("/qdsPostList?customerName=" + customerCorpName + "&" + "postHeadTab=" + 0) 
        },
        goJobSeek(corpId) {
            this.$router.push("/qdsSearchResume?corpId=" + corpId + "&" + "selected=" + 2)  
        },
        // goPosition(customerCorpName) {
        //     this.$router.push("/qdsPost?customerName=" + customerCorpName + "&" + "postHeadTab=" + 0) 
        // },
        goDetail(customerId){
            if(this.customerHeadTab == 0 ) {
                localStorage.setItem('qdsMcustomerStateValue',2)   // 标记是查看进入的
                localStorage.setItem('qdsMcustomerCustomerId',customerId)   // 标记是查看进入的
                this.$router.push('/qdsViewCustomer')
            }else if(this.customerHeadTab == 3) {
                localStorage.setItem('qdsMcustomerStateValue',4)   // 存储认证状态
                localStorage.setItem('qdsMcustomerCustomerId',customerId)   // 存储客户的id
                this.$router.push('/qdsViewCustomer')
            }
            
        },
        postHeadTapBtn(index) {
            this.customerHeadTab = index
            if(this.customerHeadTab == 0) {
                this.state.state = 2
                this.stateTime = '合作时间'
                this.customerHeadTab = 0;
                this.getListData()
            }else if(this.customerHeadTab == 1) {
                this.state.state = 1
                this.stateTime = '申请时间'
                this.customerHeadTab = 1;
                this.getListData()
            }else if(this.customerHeadTab == 2) {
                this.state.state = 3
                this.stateTime = '退回时间'                   
                this.customerHeadTab = 2;
                this.getListData()
            }else if(this.customerHeadTab == 3) {
                this.state.state = 4
                this.stateTime = '解除时间'                   
                this.customerHeadTab = 3;
                this.getListData()
            }
		},
		statistics() {     //  四种状态下的条数   qdsMToken  qdsSCorpUserId
 			var that = this;
			var token = Util.getCookie("qdsMToken");
            that.axios(that.qdsGlobalApiUrl +"ylw/qhChannelCorp/api/getCustomerCount?token=" + token,    //    "http://10.0.15.58:19000/"  that.qdsGlobalApiUrl
                    {
                        method: "get",
                        timeout:10000
                    }
            ).then(function(response) {
                if(response.data.code == 10000 ) {              
					that.recruitingNum = response.data.data.relationCount;  // 已审核数量
					that.waitingNum = response.data.data.checkCount;         // 审核中数量
					that.underReviewNum = response.data.data.checkFailedCount; // 审核失败数量
					that.hasRemovedNum = response.data.data.relieveCount;      // 解除合作
                }
            })
            .catch(function(error) {

            });           
                
		},
		getListData() {   // 首次进入 列表数据,  因为是四个状态共用一个列表, 所以每次切换状态时,要初始化参数 lastId, state
            var that = this;
            var token = Util.getCookie("qdsMToken");
			var userId = Util.getCookie("qdsSCorpUserId");
            var add = JSON.stringify(that.state)
            that.listData = []
            that.axios(that.qdsGlobalApiUrl +"ylw/qhChannelCorp/api/getChannelCustomerList?token=" + token + '&page=' + that.currentPage + '&limit=' + that.limitNum,    //    "http://10.0.15.58:19000/"  that.qdsGlobalApiUrl
                {
                    headers: {'Content-Type': 'application/json;charset=UTF-8'},
                    method: "post",
                    data: add,
                    timeout:10000
                }
            ).then(function(response) {
                if(response.data.code == 10000 && response.data.data.endRow > 0) {
                    that.listShow = true
					that.listData = response.data.data.rows
                }else {
                    that.listShow = false
                }
            })
            .catch(function(error) {

            });
		},
		infiniteRecruiting($state) {   // 全部简历 滚动分页加载
            var that = this;
            that.currentPage++;
            var token = Util.getCookie("qdsMToken");
			var userId = Util.getCookie("qdsSCorpUserId");
            var add = JSON.stringify(that.state)
            that.axios(that.qdsGlobalApiUrl +"ylw/qhChannelCorp/api/getChannelCustomerList?token=" + token + '&page=' + that.currentPage + '&limit=' + that.limitNum, 
                {
                    headers: {'Content-Type': 'application/json;charset=UTF-8'},
                    method: "post",
                    data: add,
                    timeout:10000
                }
            ).then(function(response) {
                if(response.data.code == 10000 && response.data.data.rows.length > 0) {
					that.listData = that.listData.concat(response.data.data.rows)
					$state.loaded();
                }else{
					$state.complete();
				}
            })
            .catch(function(error) {

            });
		},
		disengagement(id) {   //解除合作按钮
			let that = this
			that.customerId = id
			MessageBox({
				title: '提示',
  				message: '确认解除合作? 该客户关联的职位将全面下架。',
  				showCancelButton: true
			}).then(action => {
                if(action == 'confirm') {
                    that.trueDisengagement()
                }
			}).catch(error => {
				console.log(error)
			})
		},
		trueDisengagement() {   //解除合作  确定
            var that = this;
            var token = Util.getCookie("qdsMToken");
            that.axios(that.qdsGlobalApiUrl +"ylw/qhChannelCorp/api/relieveBinding?token=" + token,    //    "http://10.0.15.58:19000/"  that.qdsGlobalApiUrl
                {
                    method: "post",
                    params: {
                        customerId: that.customerId,     // 	用户id
                        status: 2,             //从哪一条之后开始查询
                        reason: '',        // 解除原因  在运营平台有值
                    },
                    timeout:10000
                }
            ).then(function(response) {
                if(response.data.code == 10000) {
                    that.consumerState = response.data.data
                    that.getListData()
                    that.statistics()
                }else {
                    Notify({
						message: response.data.message,
						duration: 1500,
						background: '#E6A23C'
					});
                }
            })
            .catch(function(error) {
            });
		},
		failReason(value) {
			MessageBox.alert('', { 
				message: value, 
			 	title: '失败原因', 
			 	confirmButtonText: '确定',
			 }).then((action) => { 
				
			 }).catch(error => { 
				 
			 });
		},
		cancelReason(value) {
			MessageBox.alert('', { 
				message: value, 
			 	title: '取消合作原因', 
			 	confirmButtonText: '确定',
			 }).then((action) => { 
				
			 }).catch(error => { 
				 
			 });
		},
		edit(customerId,certificationRemark) {   //编辑按钮  
            localStorage.setItem('qdsMcustomerCont','edit')   // 标记从编辑页面进入的
            localStorage.setItem('qdsMcustomerCustomerId',customerId)   // 标记是查看进入的
            
            this.$router.push({name:'qdsNewCustomer', params:{customerId:customerId}})
		},
		recooperate(id) {   //重新合作按钮
			let that = this
			that.customerId = id
			MessageBox({
				title: '提示',
  				message: '确认重新合作?',
  				showCancelButton: true
			}).then(action => {
                if(action == 'confirm') {
                    that.trueRecooperate()
                }
			}).catch(error => {

			})
			
        },
		trueRecooperate() {   //重新合作 确定
			var that = this;
            var token = Util.getCookie("qdsMToken");
            that.axios(that.qdsGlobalApiUrl +"ylw/qhChannelCorp/api/reBinding?token=" + token,    //    "http://10.0.15.58:19000/"  that.qdsGlobalApiUrl
                {
                    method: "post",
                    params: {
                        customerId: that.customerId,     // 	用户id
                    },
                    timeout:10000
                }
            ).then(function(response) {
                if(response.data.code == 10000) {
                    that.consumerState = response.data.data
                    that.getListData()
                    that.statistics()
                }else {
					Notify({
						message: response.data.message,
						duration: 1500,
						background: '#E6A23C'
					});
                }
                })
                .catch(function(error) {

                });
        },
    }
}
</script>

<style scoped>
.customer-page{
    background-color: #f5f5f5;
}
.customer-page .zhanwei{
	height: 2.4375rem;
	width: 100%;
}
.customer-page .header{
    display: -webkit-flex;
    display: flex;
    justify-content: space-around;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 2.4375rem;
    line-height: 2.4375rem;
    background-color: #fff;
}
.customer-page .header .header-item{
    position: relative;
    height: 100%;
    font-size: 1rem;
    color: #0B231F;
    text-align: center;
    cursor: pointer;
}
.customer-page .header .right-red-slot{
    position: absolute;
    top: .6875rem;
    right: .0625rem;
    display: flex;
    display: -webkit-flex;
    min-width: .75rem;
    height: .75rem;
    justify-content: center;
    align-items: center;
    -webkit-transform: translateY(-50%) translateX(100%);
    transform: translateY(-50%) translateX(100%);
    background-color: #FB5C55;
    border-radius: 50%;
    color: #fff;
    font-size: .5rem;
    text-align: center;
    border: 1px solid #fff;
}
.customer-page .header .headerItemActive{
    color: #0B231F;
}
.customer-page .header-item .isActive{
    position: absolute;
    left: 1.0625rem;
    bottom: 0;
    align-items: flex-end;
    display: inline-block;
    width: 2.0625rem;
    height: .1875rem;
    background-color: #5FBCA8;
}
.customer-page .header-item .isFirstActive{
    position: absolute;
    left: .4375rem;
    bottom: 0;
    align-items: flex-end;
    display: inline-block;
    width: 2.0625rem;
    height: .1875rem;
    background-color: #5FBCA8;
}
.customer-page .list-customer .list-item{
	margin-top: 10px;
	background: #fff;
    margin-left: .9375rem;
    margin-right: .9375rem;
}
.customer-page .list-customer .list-item .item-top {
	padding: .625rem 0;
	margin: 0 .625rem;
	display: -webkit-flex;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
	border-bottom: 1px solid #E5E5E5;
}
.customer-page .list-customer .list-item .item-top img {
	width: 2.5rem;
	height: 2.5rem;
	border: 1px solid #f5f5f5;
	border-radius: 50%;
}
.customer-page .list-customer .list-item .item-top .item-top-middle {
	width: 61%;
}
.customer-page .list-customer .list-item .item-top .item-top-middle-other {
	width: 85%;
}
.customer-page .list-customer .list-item .item-top .item-top-middle p:nth-child(1) {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	font-size: 1rem;
	color: #384242;
	font-weight: bold;
}
.customer-page .list-customer .list-item .item-top .item-top-middle p:nth-child(2) {
	font-size: .6875rem;
	color: #818D8D;
}
.customer-page .list-customer .list-item .item-top .item-top-right {
	line-height: 1.87rem;
    height: 1.6875rem;
	padding: 0 .6875rem;
	border-radius: .84375rem;
	background: #FF963D;
	font-size: .75rem;
	color: #fff;
}
.customer-page .list-customer .list-item .item-body {
	display: -webkit-flex;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
	padding-bottom: .9375rem;
}	
.customer-page .list-customer .list-item .item-body .item-body-left {
	text-align: center;
}
.customer-page .list-customer .list-item .item-body .item-body-left p:nth-child(1) {
	height: 3.125rem;
	line-height: 3.125rem;
	font-size: 1.375rem;
	color: #384242;
}
.customer-page .list-customer .list-item .item-body .item-body-left p:nth-child(2) {
	line-height: 1.7rem;
	height: 1.5rem;
	font-size: .6875rem;
	color: #2DA1A4;
	padding: 0 .375rem;
	border: 1px solid #2DA1A4;
	border-radius: 3px;
}
.customer-page .list-customer .list-item .item-body .line {
	height: 3rem;
	width: .0625rem;
	background: #E5E5E5;
	margin-top: 1rem;
}
.customer-page .list-customer .list-item .item-foot {
	display: -webkit-flex;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
	/* flex-grow: 1; */
}
.customer-page .list-customer .list-item .item-foot p:nth-child(1){
	font-size: .875rem;
	color: #fff;
	background: #FF963D;
	text-align: center;
	height: 2.1875rem;
	line-height: 2.1875rem;
	padding: 0 3.625rem;
	flex-grow: 1;
}
.customer-page .list-customer .list-item .item-foot .bg-fail{
	font-size: .875rem;
	color: #354242 !important;
	background: #E5E5E5 !important;
	text-align: center;
	height: 2.1875rem;
	line-height: 2.1875rem;
	padding: 0 3.625rem;
	flex-grow: 1;
}
.customer-page .list-customer .list-item .item-foot p:nth-child(2){
	font-size: .875rem;
	color: #fff;
	background: #ff963d;
	text-align: center;
	height: 2.1875rem;
	line-height: 2.1875rem;
	padding: 0 3.625rem;
	flex-grow: 1;
}
.list-no-data{
    padding-top: 9.5rem;
    margin: auto 0;
    text-align: center;
}
.list-no-data .bo-data-icon{
    display: inline-block;
    width: 12.15625rem;
    height: 6.96875rem;
}
.list-no-data .recruiting-tips{
	height: 2.5rem;
	line-height: 2.5rem;
    font-size: 14px;
    color: #354242;
}
.infinite-load{
    display: inline-block;
    margin-top: 1rem;
}

.customer-page .list-customer .list-item .item-foot .bg-success{
	color: #354242 !important;
	background: #E5E5E5 !important;
}
</style>





